<template>
    <div>
        <div class="box">
            <h2>嵌套操作</h2>
            <el-popover
                placement="top"
                v-model="visible2">
                <p>这是一段内容这是一段内容确定删除吗？</p>
                <div style="text-align: right; margin: 0">
                    <el-button size="mini" plain @click="visible2 = false">取消</el-button>
                    <el-button type="primary" size="mini" @click="visible2 = false">确定</el-button>
                </div>
                <el-button slot="reference" plain>删除</el-button>
            </el-popover>
        </div>
    </div>
</template>
<script>
export default {
    data () {
        return {
            visible2: false
        }
    },
    methods: {
        handleSearch () {
            console.log('search!')
        }
    }
}
</script>
<style scoped>
.el-row {
    margin-bottom: 20px;
    &:last-child {
        margin-bottom: 0;
    }
}
.el-col {
    border-radius: 4px;
}

.box {
    margin-bottom: 20px;
}
</style>
/*按钮样式*/
<style>
/*按钮总样式覆盖*/
.el-button {
    font-size: 12px;
    padding: 9px 16px;
    border-radius: 2px;
}
/*按钮size样式*/
.el-button--medium {
    padding: 0px 16px;
    height: 40px;
    line-height: 38px;
    font-size: 14px;
    min-width: 104px;
}
.el-button--medium.is-round {
    padding: 0px 20px;
}
.el-button--mini {
    padding: 0px 8px;
    height: 24px;
    line-height: 22px;
    font-size: 12px;
    min-width: 56px;
}
.el-button--mini.is-round {
    padding: 0px 15px;
}
/* 朴素按钮的颜色覆盖*/
.el-button.is-plain:focus,
.el-button.is-plain:hover {
    outline: none;
    text-decoration: none;
    color: #1acac6;
    background: #FFF;
    border-color: #1acac6;
}
/*禁止类按钮样式*/
.el-button.is-disabled,
.el-button.is-disabled:focus,
.el-button.is-disabled:hover {
    cursor: not-allowed;
    background: #f8f8f8;
    color: #ccc;
    border: 1px solid #ebeef5;
    -ms-transform: none;
    transform: none;
}
/*primary 类按钮样式修改*/
.el-button--primary {
    color: #fff;
    background-color: #00C4C0;
    border-color: #00C4C0;
}
.el-button--primary:focus, .el-button--primary:hover {
    background: #1acac6;
    border-color: #1acac6;
    color: #fff;
}
/*text样式覆盖*/
.el-button--text.is-disabled,
.el-button--text.is-disabled:focus,
.el-button--text.is-disabled:hover {
    border-color: transparent;
    color: #c0c4cc;
    cursor: not-allowed;
    background-image: none;
    background-color: #fff;
}
.el-button--text {
    color: #00C4C0;
}
.el-button--text:active,
.el-button--text:focus,
.el-button--text:hover {
    color: #1acac6;
    border-color: transparent;
    background-color: transparent;
}
/*危险累按钮样式覆盖*/
.el-button--danger.is-plain {
    color: #F86B6B;
    background: #fff;
    border-color: #F86B6B;
}
.el-button--danger.is-plain:focus, .el-button--danger.is-plain:hover {
    background: #fff;
    border-color: #f97a7a;
    color: #f97a7a;
}
/*loading按钮样式*/
.el-button.is-loading {
    cursor: not-allowed;
    background: #f8f8f8;
    color: #ccc;
    border: 1px solid #eee;
    -ms-transform: none;
    transform: none;
}
</style>

<style>
/*样式覆盖*/
.el-popover {
    font-size: 12px;
    color: #666;
    padding: 8px 16px;
    box-shadow: 0 1px 6px rgba(0, 0, 0, 0.2);
    line-height: 1.5;
}
.el-button+.el-button {
    margin-left: 0px;
}
</style>



